import React from 'react';
import { Table } from 'react-bootstrap';
import { IMAGES } from '../../../assets/images';

const Unpaid = () => {
    return (
        <Table responsive>
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Reason</th>
                    <th>Session #</th>
                    <th>Payment</th>
                    <th>Status</th>
                    <th>Due Date</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {UnpaidList.map((list, index) => {
                    return (
                        <TableListData list={list} key={index} />
                    );
                })}
            </tbody>
        </Table>
    );
}

export default Unpaid;
function TableListData({ list }) {
    return (
        <tr>
            <td>{list?.name}</td>
            <td>{list?.reason}</td>
            <td>{list?.session}</td>
            <td>{list?.payment}</td>
            <td>
                <span
                    className={`${list?.status === true
                        ? "low_risk_btn"
                        : "medium_risk_btn"
                        }`}
                >
                    {list?.status === true ? "Paid" : "unPaid"}
                </span>
            </td>
            <td>{list?.date}</td>
            <td>
                <button type='button' className='notes-added-btn'>
                    Send Reminder
                </button>
            </td>
        </tr>
    )
}

