import React from 'react';
import { Table } from 'react-bootstrap';
import { HiVideoCamera } from "react-icons/hi";
import { IMAGES } from '../../../assets/images';

const Paid = () => {
    return (
        <Table responsive>
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Reason</th>
                    <th>Session #</th>
                    <th>Status</th>
                    <th>Note</th>
                    <th>Daily Mode Tracker</th>
                    <th>Date and Time</th>
                </tr>
            </thead>
            <tbody>
                {PaidList.map((list, index) => {
                    return (
                        <TableListData list={list} key={index} />
                    );
                })}
            </tbody>
        </Table>
    );
}

export default Paid;


function TableListData({ list }) {
    return (
        <tr>
            <td>{list?.name}</td>
            <td>{list?.reason}</td>
            <td>{list?.session}</td>
            <td>
                <span
                    className={`${list?.status === true
                        ? "low_risk_btn"
                        : "medium_risk_btn"
                        }`}
                >
                    {list?.status === true ? "Paid" : "unPaid"}
                </span>
            </td>
            <td><span className="file-btn">File</span></td>
            <td><span className="file-btn">File</span></td>
            <td>{list?.date}</td>
        </tr>
    )
}

